.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

.hover-underline-animation {
  display: inline-block;
  position: relative;
  color: hsl(0deg, 0%, 100%);
}

.hover-underline-animation:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: hsl(0deg, 0%, 100%);
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.hover-underline-animation:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Alata", sans-serif;
  font-size: 1.5rem;
}

h1, h2, h3 {
  font-family: "Josefin Sans", sans-serif;
  color: hsl(0deg, 0%, 0%);
  margin: 0;
  font-weight: 300;
  text-transform: uppercase;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

a {
  font-family: "Alata", sans-serif;
}

p {
  font-family: "Alata", sans-serif;
  color: hsl(0deg, 0%, 41%);
  font-size: 1.5rem;
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
  max-width: 1300px;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  background-image: url(../img/mobile/image-hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 65rem;
  position: relative;
}
.header .mobile-menu {
  z-index: -1;
  height: 0;
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: hsl(0deg, 0%, 0%);
  width: 100%;
  justify-content: center;
  padding-left: 2rem;
  transition: 0.3s;
}
.header .mobile-menu a {
  font-family: "Josefin Sans", sans-serif;
  font-size: 4rem;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
  padding: 1.5rem 0;
}
.header::after {
  content: "";
  background-color: hsl(0deg, 0%, 0%);
  opacity: 0.4;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
@media (min-width: 480px) {
  .header {
    background-image: url(../img/desktop/image-hero.jpg);
  }
}
.header .content {
  position: relative;
}
.header .content .header-content {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  z-index: 10;
  padding: 4rem 2rem;
}
@media (min-width: 1024px) {
  .header .content .header-content {
    padding: 4rem 0;
  }
}
.header .content .header-content .logo {
  width: 14rem;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header .content .header-content .logo {
    width: 20rem;
  }
}
.header .content .header-content .logo-menu {
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header .content .header-content .logo-menu {
    display: none;
  }
}
.header .content .header-content .nav-menu {
  display: none;
  justify-content: space-between;
  min-width: 40%;
}
@media (min-width: 1024px) {
  .header .content .header-content .nav-menu {
    display: flex;
  }
}
.header .content .header-content .nav-menu a {
  color: hsl(0deg, 0%, 100%);
  font-size: 1.8rem;
  line-height: 3;
}
.header .content .hero-content {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 65rem;
  position: absolute;
}
@media (min-width: 1024px) {
  .header .content .hero-content {
    justify-content: flex-start;
  }
}
.header .content .hero-content .text-hero {
  width: 100%;
  margin: 0 3rem;
  padding: 2rem;
  border: 2px solid hsl(0deg, 0%, 100%);
  z-index: 1;
}
@media (min-width: 768px) {
  .header .content .hero-content .text-hero {
    max-width: 55%;
    margin: 0;
  }
}
.header .content .hero-content .text-hero h1 {
  margin: 0;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
  font-size: 4rem;
  line-height: 1;
}
@media (min-width: 768px) {
  .header .content .hero-content .text-hero h1 {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .header .content .hero-content .text-hero h1 {
    font-size: 7.2rem;
  }
}

.about {
  margin: 10rem 0 5rem 0;
}
@media (min-width: 1024px) {
  .about {
    margin: 10rem 0;
  }
}
@media (min-width: 1024px) {
  .about .about-content {
    background-image: url(../img/desktop/image-interactive.jpg);
    background-repeat: no-repeat;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 10rem);
  }
}
.about .about-content .imagen {
  padding: 0 2rem;
}
@media (min-width: 1024px) {
  .about .about-content .imagen {
    display: none;
  }
}
.about .about-content .imagen img {
  margin: 0 auto;
}
.about .about-content .text {
  padding: 3rem 3rem;
  text-align: center;
}
@media (min-width: 1024px) {
  .about .about-content .text {
    grid-column: 4/8;
    grid-row: 3;
    background-color: hsl(0deg, 0%, 100%);
    height: fit-content;
    padding: 5rem;
  }
}
.about .about-content .text h1 {
  font-size: 3.5rem;
  margin: 2rem 0;
}
@media (min-width: 1024px) {
  .about .about-content .text h1 {
    font-size: 4.8rem;
    text-align: left;
  }
}
.about .about-content .text p {
  line-height: 2;
}
@media (min-width: 1024px) {
  .about .about-content .text p {
    text-align: justify;
  }
}
@media (min-width: 1024px) {
  .about .about-content .text h1, .about .about-content .text p {
    padding: 0 2.5rem;
  }
}

.our-creations .our-creations-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-bottom: 10rem;
}
.our-creations .our-creations-content .content-header {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
@media (min-width: 768px) {
  .our-creations .our-creations-content .content-header {
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}
.our-creations .our-creations-content .content-header .heading {
  font-size: 3.5rem;
  margin: 2rem 0;
}
.our-creations .our-creations-content .content-header .button {
  text-transform: uppercase;
  color: hsl(0deg, 0%, 0%);
  letter-spacing: 0.5rem;
  line-height: 0;
  padding: 2rem 4rem;
  border: 0.1rem solid hsl(0deg, 0%, 0%);
  position: absolute;
  bottom: 3rem;
  transition: 0.2s ease-in-out all;
}
@media (min-width: 768px) {
  .our-creations .our-creations-content .content-header .button {
    position: initial;
  }
}
.our-creations .our-creations-content .content-header .button:hover {
  background-color: hsl(0deg, 0%, 0%);
  color: hsl(0deg, 0%, 100%);
}
.our-creations .our-creations-content .creations {
  display: grid;
  width: 100%;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .our-creations .our-creations-content .creations {
    grid-template-columns: repeat(2, auto);
    gap: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .our-creations .our-creations-content .creations {
    grid-template-columns: repeat(4, auto);
    gap: 2.5rem;
  }
}
.our-creations .our-creations-content .creations .creation {
  position: relative;
  cursor: pointer;
  height: 100%;
}
.our-creations .our-creations-content .creations .creation img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.our-creations .our-creations-content .creations .creation .creation-mask {
  height: 100%;
  width: 100%;
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  transition: 0.2s ease-in-out all;
  left: 0;
  top: 0;
}
.our-creations .our-creations-content .creations .creation .creation-mask:hover {
  background: rgba(255, 255, 255, 0.6);
}
.our-creations .our-creations-content .creations .creation h1 {
  position: absolute;
  font-size: 2.8rem;
  left: 2rem;
  bottom: 1rem;
  width: 50%;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
  transition: 0.2s ease-in-out all;
}
.our-creations .our-creations-content .creations .creation:hover h1 {
  color: hsl(0deg, 0%, 41%);
}

.footer {
  background-color: hsl(0deg, 0%, 0%);
  padding: 3rem 0;
}
.footer .footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1024px) {
  .footer .footer-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 5rem);
  }
}
.footer .footer-content .logo {
  width: 15rem;
  padding-bottom: 2rem;
}
@media (min-width: 1024px) {
  .footer .footer-content .logo {
    padding-bottom: 0;
  }
}
.footer .footer-content .links, .footer .footer-content .socials {
  color: hsl(0deg, 0%, 100%);
}
.footer .footer-content .links {
  list-style: none;
  padding: 2rem 0;
  margin: 0;
}
@media (min-width: 1024px) {
  .footer .footer-content .links {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    grid-row: 2;
  }
}
.footer .footer-content .links li {
  line-height: 3;
  font-size: 1.5rem;
}
.footer .footer-content .links li a {
  color: hsl(0deg, 0%, 100%);
}
.footer .footer-content .socials {
  align-self: flex-end;
  display: flex;
  justify-content: space-between;
  font-size: 3rem;
  gap: 2rem;
  margin: 0 auto;
  width: 50%;
  padding: 1rem 0;
}
@media (min-width: 1024px) {
  .footer .footer-content .socials {
    margin: 0 0 0 auto;
    width: 50%;
  }
}
.footer .footer-content .socials .social i {
  padding-bottom: 0.5rem;
  cursor: pointer;
}
.footer .footer-content .copyright {
  margin: 0;
  font-size: 1.6rem;
}
@media (min-width: 1024px) {
  .footer .footer-content .copyright {
    text-align: right;
  }
}